/* Style form inputs and select */
input[type="date"],
input[type="number"],
input[type="text"],
select,
textarea {
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid rgb(0, 0, 0);
  border-radius: 4px;
  width: 100%;
}

/* Style the multi-select dropdown */
select[multiple] {
  height: auto;
}

/* Style the submit button */
button[type="submit"] {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #cc37c4ac;
}

textarea{
    resize: none;
}