.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #007bff;
    color: white;
    padding: 1rem;
  }
  
  .navbar-logo {
    font-size: 1.5rem;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
  }
  
  .navbar-links li {
    margin-right: 1rem;
  }

  .navbar-links a {
    text-decoration: none;
    color: white;
  }
  
  .navbar-links a:hover {
    color: #66b3ff;
  }